const Image = ({ image }) => (
  <div>
    <div className="columns portfolio-item">
      <div className="item-wrap">
        <a href={"#" + image.modal} >

          <img
            src={image.source}
            alt='Proximamente'
          />
          <p>{image.descripcion}</p>

          <div className="overlay">
            <div className="portfolio-item-meta">
              <h5>{image.nombre}</h5>
            </div>
          </div>
        </a>
      </div>
    </div>
    <div id={image.modal} className="popup-modal mfp-hide">
      <img className="scale-with-grid" src={image.obra} alt="" />
      <div className="description-box">
        <h4>{image.frase}</h4>
        <span className="categories"><i className="fa fa-tag" /><a href="#">Fuente</a></span>
      </div>
      <div className="link-box">
        <a className="popup-modal-dismiss">Close</a>
      </div>
    </div>
  </div>
)

export default class Artistas extends Component {

  render() {
    return (

      <section id="portfolio">
        <div className="row">
          <div className="twelve columns collapsed">
            <h1 className="gestores">Artistas</h1>
            <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
              {artistas.map(image => (
                <Image
                  image={image}
                  key={image.selector}
                />
              ))}

            </div>
          </div>
        </div>
      </section>



    )

  }

} 
