import React, { Component } from 'react';
import './cards.css';
const images = [
    {
        nombre: 'Johanna Velandia',
        source: 'images/gestores/johana.png',
        descripcion: 'Johanna Velandia es diseñadora industrial de la universidad Jorge Tadeo Lozano, Magister en docencia con énfasis en investigación. Con experiencia académica enfocada en la dirección de Proyectos de grado.',
        id: 1
    },
    {
        nombre: 'Luis Velandia',
        source: 'images/gestores/luis.png',
        descripcion: 'Luis Velandia es médico anestesiólogo; egresado de la Universidad Militar Nueva Granada. Artista plástico, poeta. Publicado en antología de poesía en el cine con el Festival de cine de Bogotá y Editorial Escarabajo 2019.',
        id: 2
    },
    {
        nombre: 'Andrés Alarcon',
        source: 'images/gestores/andres.png',
        descripcion: 'Andrés Alarcón es un artista plástico colombiano, nacido en Bogotá, profesor en la Escuela de Artes Guerrero que se proyecta como una promesa de la pintura figurativa contemporánea. Le atrae el realismo y sentir como se transforman las cosas que observa mientras pinta sobre el lienzo, y así generar un acercamiento para crear una ilusión.',
        id: 3
    },


]
const Image = ({ image }) => (
    <div className="card">
        <h1 className="titulo">{image.nombre}</h1>
        <img
            src={image.source}
            alt=''
        />
        <p>{image.descripcion}</p>

    </div>
)

export default class Gestores extends Component {
    render() {
        return (


            <section id="gestores">
                <h1 className="gestores">Gestores</h1>
                <div className="container">
                    {images.map(image => (
                        <Image
                            image={image}
                            key={image.id}
                        />
                    ))}
                </div>
            </section>
        )
    }
}
